body {
  background-color: #e5e5e5;
  font-family: "Lato", sans-serif;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
}

section {
  background-color: #fff;
}

.header {
  width: 100%;
  padding-top: 19px;
  background-color: #fff;

  &__title {
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 36px;
    color: #333e50;
    margin-bottom: 15px;
  }

  &__info {
    margin-bottom: 20px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: #333e50;
    text-align: justify;
  }

  &__detailed {
    display: none;
  }

  &__line {
    width: 100%;
    height: 1px;
    background-color: #e8ecef;
  }
}

.navigation {
  width: 100%;

  &__list {
    display: flex;
    justify-content: space-between;
    border: 1px solid #e8ecef;
  }

  &__item {
    width: calc(100% / 6);
    border: 1px solid #f1f4f7;
    border-top: none;
    border-bottom: none;
    padding: 13px 0 14px;

    &:first-of-type {
      border-left: none;
    }

    &:last-of-type {
      border-right: none;
    }

    &:hover {
      background-color: #f8fbfe;
      cursor: url(../../images/cursor.png), pointer;
      transition: all 0.2s linear;
    }

    &:hover .navigation__text {
      color: #95ce5a;
      transition: all 0.2s linear;
    }
  }

  &__img {
    display: block;
    margin: auto;
    margin-bottom: 20px;
  }

  &__text {
    display: block;
    text-align: center;
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    line-height: 19px;
    align-items: center;
    color: #333e50;
  }
}

.card-choose {
  width: 100%;
  padding: 20px 0;

  &__block {
    display: flex;
    justify-content: space-between;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    align-items: center;
    text-align: right;
    color: #adb4bf;
  }

  &__info {
    display: flex;
    justify-content: space-between;
  }

  &__text {
    display: block;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    color: #adb4bf;
  }

  &__update {
    display: flex;
    justify-content: space-between;
  }

  &__date {
    margin-left: 3px;
  }

  &__sort {
    margin-left: 10px;
  }

  &__select {
    border: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: none;
    color: #388ec3;
    cursor: url(../../images/cursor.png), pointer;
    border-bottom: 1px dashed #388ec3;
  }
}

.cards {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  box-shadow: 0px 4px 20px #eef0f2;
  border-radius: 5px;
  margin-bottom: 20px;

  &__info {
    padding: 20px 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  &__name {
    display: inline-block;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    align-items: center;
    color: #388ec3;
    margin-bottom: 5px;
    box-sizing: border-box;

    &:hover {
      cursor: url(../../images/cursor.png), pointer;
      color: #95ce5a;
      transition: all 0.2s linear;
    }
  }

  &__types {
    justify-content: start;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    color: #9299a2;
  }

  &__type {
    margin-right: 20px;
    margin-bottom: 12px;
  }

  &__description {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    color: #333e50;
    text-align: justify;
  }

  &__item {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #333e50;
    margin-bottom: 25px;
    text-align: justify;

    &:last-of-type {
      margin-bottom: 0;
    }

    &_bold {
      font-weight: 900;
    }

    &::before {
      display: inline-block;
      content: url(../../images/checked_green.svg);
      margin-right: 10px;
    }
  }

  &__display {
    margin-left: 60px;
  }

  &__icons {
    justify-content: center;
    display: flex;
    padding: 10px;
  }

  &__icon {
    margin-left: 17px;

    &:first-of-type {
      margin-left: 0;
    }
  }

  &__popularity {
    padding: 10px 0;
    display: flex;
    justify-content: space-around;
  }

  &__feedbacks {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    text-align: right;
    color: #388ec3;
  }

  &__footer {
    width: 100%;
    padding: 20px 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  &__numbers {
    width: 80%;
    max-width: 860px;
  }

  &__numbersList {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
  }

  &__numbersItem {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    // text-align: center;
    color: #333e50;

    & span {
      display: block;
    }

    &_grey {
      font-size: 13px;
      line-height: 16px;
      color: #9299a2;
    }

    &_bold {
      font-weight: 900;
    }
  }

  &__benefits {
    display: flex;
    justify-content: start;
  }

  &__benefit {
    padding: 5px 10px;
    border: 1px solid #dadee2;
    box-sizing: border-box;
    border-radius: 50px;
    margin-left: 5px;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #9299a2;

    &:first-of-type {
      margin-left: 0;
    }

    &:hover {
      background: #ff8066;
      color: #ffffff;
      cursor: url(../../images/cursor.png), pointer;
      transition: all 0.2s linear;
    }

    &_active {
      background: #ff8066;
      color: #ffffff;
    }
  }

  &__button {
    background: #95ce5a;
    box-shadow: 0px 5px 10px rgba(149, 206, 90, 0.4);
    border-radius: 5px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    align-items: center;
    text-align: center;
    color: #ffffff;
    padding: 10px 45px;
    display: inline-block;
    align-self: center;
    z-index: 2;

    &_white {
      background: #ffffff;
      border: 1px solid #dadee2;
      box-sizing: border-box;
      border-radius: 5px;
      padding: 10px 50px;
      text-align: center;

      &:hover {
        cursor: pointer;
        border: 1px solid #333e50;
        box-sizing: border-box;
        transition: all 0.2s linear;
      }
    }

    &:hover {
      cursor: pointer;
      box-shadow: 0px 5px 8px rgba(149, 206, 90, 0.8);
      transition: all 0.2s linear;
      background: #8cc459;
    }

    &:active {
      background: #95ce5a;
      border: 1px solid #333e50;
      box-sizing: border-box;
      box-shadow: 0px 5px 10px rgba(149, 206, 90, 0.4);
      border-radius: 5px;
      color: #333e50;
    }
  }

  &__count {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    display: block;
    align-items: center;
    text-align: center;
    padding-top: 25px;
    color: #adb4bf;
  }
}

.detailed {
  padding: 10px 0 0;

  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    align-items: center;
    color: #388ec3;
    cursor: pointer;

    &::after {
      display: inline-block;
      content: url(../../images/arrow_bottom_blue.svg);
      margin-left: 3px;
      margin-bottom: 2px;
    }

    &:hover {
      color: #95ce5a;
      transition: all 0.2s linear;
    }

    &:hover::after svg {
      color: #95ce5a;
      transition: all 0.2s linear;
    }
  }
}

.pagination {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  margin-top: -5px;

  &__block {
    box-shadow: 0px 4px 20px #eef0f2;
    border-radius: 5px;
    padding: 20px 0;
  }

  &__nav {
    display: flex;
    justify-content: center;
  }

  &__item {
    display: block;
    padding: 15px 19px;
    border: 1px solid #dadee2;
    box-sizing: border-box;
    border-right: none;
    cursor: pointer;

    &:hover {
      background-color: #aeb4bf;
      transition: all 0.2s linear;
    }

    &_prev {
      background: #f7fafc;
      border-radius: 3px 0px 0px 3px;

      &::before {
        display: block;
        content: url(../../images/arrow_left.svg);
      }
    }

    &_next {
      background: #f7fafc;
      border-radius: 0px 3px 3px 0px;
      border: 1px solid #dadee2;

      &::before {
        display: block;
        content: url(../../images/arrow_left.svg);
        transform: rotate(180deg);
      }
    }
  }
}

.feedback {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  margin-top: 30px;
  background-image: url(../../images/cards.png);
  background-repeat: no-repeat;
  background-position: right;
  background-position-x: calc(100% - 60px);

  &__block {
    box-shadow: 0px 4px 20px #eef0f2;
    border-radius: 5px;
    padding: 20px;
    position: relative;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;

    & .cards__button {
      padding: 10px 25px;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 900;
    font-size: 22px;
    line-height: 32px;
    display: flex;
    align-items: center;
    color: #333e50;
    margin-bottom: 10px;
  }

  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    color: #333e50;
  }

  &__img {
    position: absolute;
    right: 45px;
    top: 0;
  }
}

@media (max-width: 1199px) {
  .header {
    &__text {
      padding: 0 20px 0;
      box-sizing: border-box;
    }
  }

  .card-choose {
    &__block {
      display: block;
      padding: 0 20px 0;
      box-sizing: border-box;
      text-align: left;
    }

    &__text {
      margin-bottom: 10px;
    }

    &__update {
      display: block;
    }
  }

  .cards {
    &__button {
      padding: 10px 40px;
      width: 100%;
      box-sizing: border-box;
    }

    &__numbers {
      width: 100%;
      max-width: 100%;
    }

    &__footer {
      display: block;
    }

    &__benefits {
      margin-bottom: 20px;
    }
  }

  .feedback {
    background-position: calc(100% + 120px) calc(100% - 20px);
    &__block {
      display: block;
      margin: 0;

      & .cards__button {
        width: 70%;
      }
    }
  }
}

@media (max-width: 767px) {
  .container {
    width: calc(100% - 20 px);
  }

  .header {
    &__detailed {
      display: block;
      margin-bottom: 20px;
    }

    &__info {
      height: 40px;
      overflow: hidden;

      &_show {
        height: auto;
        animation-name: animate;
      }
    }
  }

  .navigation {
    &__item {
      display: flex;
      justify-content: center;
      width: 50%;
      flex-wrap: nowrap;
      overflow: hidden;
    }

    &__img {
      margin-bottom: 0;
    }

    &__text {
      display: flex;
    }
  }

  .card-choose {
    &__info {
      display: block;
    }

    &__update {
      margin-bottom: 10px;
    }

    &__sort {
      margin-left: 0;
    }

    &__text {
      margin-bottom: 0;
    }
  }

  .cards {
    &__info {
      display: block;
    }

    &__name {
      display: block;
      text-align: center;
    }

    &__types {
      display: block;
      text-align: center;
    }

    &__type:last-of-type {
      display: none;
    }

    &__display {
      margin-left: 0;
    }
  }
}
